.popup {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, .5);
  display: none;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s, visibility 0.5s linear 0.5s;
}